import { Box } from '@mui/material';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useCookies } from 'react-cookie';
import { UserProfile } from '../components';

const Administration = () => {
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const [cookies, setCookie, removeCookie] = useCookies<any>(['user']);

    const [users, setUsers] = useState<any>([{}]);

    const [isAdmin, setIsAdmin] = useState<Boolean | string>('checking');

    const verifyAdmin = async () => {
        const response = await axios.get(
            (process.env.REACT_APP_LOCAL_API_URL ||
                'https://ken-yokohama-mern-bug-tracker.onrender.com/') +
                'getUsers',
            {
                headers: {
                    'x-access-token': cookies.AuthToken,
                    email: cookies.Email,
                },
            }
        );
        if (response.data !== 'Not Admin') {
            setUsers(response.data);
            setIsAdmin(true);
        } else {
            setUsers(null);
            setIsAdmin(false);
        }
    };

    useEffect(() => {
        verifyAdmin();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return (
        <div>
            <Box
                id="Padding for Menu"
                sx={{
                    '@media(max-width: 700px)': {
                        height: '3.1rem',
                    },
                }}
            ></Box>
            {isAdmin === 'checking' ? (
                <p>Verifying Admin...</p>
            ) : isAdmin ? (
                <Box
                    sx={{
                        display: 'grid',
                        gridTemplateColumns: '1fr 1fr 1fr',
                        '@media (max-width: 1000px)': {
                            gridTemplateColumns: '1fr 1fr',
                        },
                        '@media (max-width: 700px)': {
                            gridTemplateColumns: '1fr',
                        },
                    }}
                >
                    {users?.map((user: any, index: number) => {
                        return (
                            <UserProfile
                                key={index}
                                id={user?._id}
                                email={user?.email}
                                role={user?.role}
                                dateRegistered={user?.dateRegistered}
                                ipAddress={user?.ipAddress}
                            />
                        );
                    })}
                </Box>
            ) : (
                <p>Access Denied, Please Use an Admin Account</p>
            )}
        </div>
    );
};

export default Administration;
